// form
$templateColor: #707f8c;
.formBox {
  width: 100%;
  background-color: #fff;
  padding: 40px;
  border: 1px solid rgca(0, 0, 0, 0.1);
  box-shadow: 0 5px 10px rgba(0, 0, 0, 0.2);
  position: relative;

  .close {
    color: red;
    text-shadow: 0 1px 0 red;
    opacity: .8;
    position: absolute;
    // top: 29px;
    // right: 36px;
    top: -11px;
    right: -4px;
    z-index: 1;
    transition: opacity .3s ease-in-out;
    font-weight: 800;

    &:hover{
      opacity: .5;
    }
  }

  h2 {
    margin: 0 auto 40px;
    padding: 0;
    color: $templateColor;
  }

  input {
    padding: 10px 0;
    margin-bottom: 30px;
  }

  textarea {
    height: 80px;
    padding: 10px 0;
    margin-bottom: 40px;
  }

  input,
  textarea,
  select {
    box-sizing: border-box;
    width: 100%;
    box-shadow: none;
    outline: none;
    border: none;
    border-bottom: 2px solid #999;

    // 移動

    // 搭配 required tag
    // &:valid ~ label,
    &:focus ~ label,
    &.isCompleted ~ label {
      top: -12px;
      left: 0;
      color: $templateColor;
      font-size: 12px;
      font-weight: bold;
    }

    // &:valid,
    &:focus,
    &.isCompleted {
      border-bottom: 2px solid $templateColor;
    }

    &:focus ~ i {
      color: #bdc5cd;
    }
  }

  .required {
    color: red;
    font-size: 14px;
  }

  [type='submit'] {
    border: none;
    cursor: pointer;
    background-color: $templateColor;
    color: #fff;
    margin-bottom: 0;
    width: 100%;
    padding: 10px 0;
  }

  form {
    div {
      position: relative;

      label {
        position: absolute;
        top: 10px;
        left: 0;
        color: #999;
        transition: top 0.2s ease-in-out;
        pointer-events: none;
      }

      i {
        position: absolute;
        top: 20px;
        right: 4px;
        pointer-events: none;
        color: $templateColor;
      }
    }
  }
}

.cRed {
  color: red;
  transition: color 0.3s ease-in-out;
}

.cRedHover:hover {
  color: #b63e3e;
}

.font-20 {
  font-size: 20px;
}

.point-none {
  pointer-events: none;
}

.pl-13{
  padding-left: 13px;
}